","file":"main.css","sourcesContent":["/* ==========================================================================\n   1. IMPORT GOOGLE FONTS\n   ========================================================================== */\n@import url(\"https://fonts.googleapis.com/css?family=Montserrat:500,700|Open+Sans\");\n/* ==========================================================================\n   2. GENERAL\n   ========================================================================== */\nbody {\n  font-family: 'Open Sans', sans-serif;\n  color: #546E7A;\n  font-size: 14px;\n  font-weight: 400;\n  background: #fff;\n  overflow-x: hidden;\n}\n\nhtml {\n  overflow-x: hidden;\n}\n\np {\n  font-size: 14px;\n  color: #546E7A;\n  line-height: 26px;\n  font-family: 'Open Sans', sans-serif;\n  margin-bottom: 0;\n}\n\na:hover, a:focus {\n  color: #f39c12;\n}\n\na {\n  color: #f39c12;\n  -webkit-transition: all 0.3s ease-in-out;\n  -moz-transition: all 0.3s ease-in-out;\n  -o-transition: all 0.3s ease-in-out;\n  transition: all 0.3s ease-in-out;\n}\n\nh1, h2, h3, h4, h5 {\n  font-family: 'Montserrat', sans-serif;\n  font-size: 40px;\n  font-weight: 700;\n  color: #263238;\n}\n\nul {\n  margin: 0;\n  padding: 0;\n}\n\nul li {\n  list-style: none;\n}\n\na:hover,\na:focus {\n  text-decoration: none;\n  outline: none;\n}\n\na:not([href]):not([tabindex]) {\n  color: #fff;\n}\n\na:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {\n  color: #fff;\n}\n\n.bg-transparent {\n  background: transparent !important;\n}\n\n.bg-defult {\n  background: #f39c12 !important;\n}\n\n/* ==========================================================================\n   2.1 Section Title\n   ========================================================================== */\n.section {\n  padding: 80px 0;\n}\n\n.section-header {\n  color: #fff;\n  margin-bottom: 40px;\n  text-align: center;\n  position: relative;\n}\n\n.section-header .section-title {\n  font-size: 36px;\n  margin-bottom: 20