}\n\n.btn-lg {\n  padding: 14px 33px;\n  text-transform: uppercase;\n  font-size: 16px;\n}\n\n.btn-rm {\n  padding: 7px 10px;\n  text-transform: capitalize;\n}\n\nbutton:focus {\n  outline: none !important;\n}\n\n.clear {\n  clear: both;\n}\n\n/* ==========================================================================\n   2.3 Social Iocns\n   ========================================================================== */\n.social-icons {\n  margin-bottom: 20px;\n}\n\n.social-icons ul {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n.social-icons ul li {\n  display: inline;\n}\n\n.social-icons ul li a {\n  display: inline-block;\n  margin-left: 5px;\n  margin-right: 5px;\n  margin-bottom: 15px;\n  border-radius: 4px;\n  border: 1px solid rgba(255, 254, 254, 0.07);\n  line-height: 40px;\n  width: 40px;\n  height: 40px;\n  text-align: center;\n  font-size: 16px;\n}\n\n.social-icons ul li a:hover {\n  color: #fff;\n}\n\n.facebook a {\n  color: #4867AA;\n}\n\n.facebook a:hover {\n  background: #4867AA;\n}\n\n.twitter a {\n  color: #1DA1F2;\n}\n\n.twitter a:hover {\n  background: #1DA1F2;\n}\n\n.google-plus a {\n  color: #DD4D42;\n}\n\n.google-plus a:hover {\n  background: #DD4D42;\n}\n\n.youtube a {\n  color: #DF2926;\n}\n\n.youtube a:hover {\n  background: #DF2926;\n}\n\n.linkedin a {\n  color: #007BB6;\n}\n\n.linkedin a:hover {\n  background: #007BB6;\n}\n\n.pinterest a {\n  color: #BD081C;\n}\n\n.pinterest a:hover {\n  background: #BD081C;\n}\n\n.dribbble a {\n  color: #EA4C89;\n}\n\n.dribbble a:hover {\n  background: #EA4C89;\n}\n\n.behance a {\n  color: #0B7CFF;\n}\n\n.behance a:hover {\n  background: #0B7CFF;\n}\n\n/* ==========================================================================\n   2.4  Pre Loader\n   ========================================================================== */\n#loader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: #263238;\n  z-index: 9999999999;\n}\n\n.spinner {\n  width: 40px;